import React, {useState, createContext} from 'react'
export  const ExamContextProvider= createContext();
const ExamContext = (props) => {
   
    const [isExamSelected , setIsExamSelected ]=useState(false);
    const [selectedCourse, setCourse]=useState('');
    const setSubject=(selectedSubject)=>{
        setCourse(selectedSubject);
    }
    return (
        
            <ExamContextProvider.Provider value={{ setSubject,setIsExamSelected, isExamSelected, selectedCourse}}>
             {props.children} 
            </ExamContextProvider.Provider>
      
    )
}

export default ExamContext
