import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { ExamContextProvider } from './ExamContext';

import { faChevronCircleRight, faChevronCircleLeft, faStop} from '@fortawesome/free-solid-svg-icons';
import Answers from './Answers';

const qs=[
    {
        question:`By 1910, the car way already conquering the highway, the private car was now part of the 
        rich man's establishment, although its price made it as easy for non luxurious individual, but for 
        the yourng ment it was`,
        A:'People no longer used train with the advent of cars and',
        B:'Significant improvement occured in road transportation since the advent of car lorries and motor cycles',
        C:'Human society was static without the express speed of cars and motorcycles',
        D:'Society would be better off without the chaotic volume and speed of motor cars lories and motor cars'

    },
   

]
export default class Testpage extends Component {
    constructor(props){
        super(props);
        this.state={
            questions:[
                {
                    question:`By 1910, the car way already conquering the highway, the private car was now part of the 
                    rich man's establishment, although its price made it as easy for non luxurious individual, but for 
                    the yourng ment it was`,
                    A:'People no longer used train with the advent of cars and',
                    B:'Significant improvement occured in road transportation since the advent of car lorries and motor cycles',
                    C:'Human society was static without the express speed of cars and motorcycles ',
                    D:'Society would be better off without the chaotic volume and speed of motor cars lories and motor cars'
            
                },
                {
                    question:`the car way already conquering the highway, the private car was now part of the 
                    rich man's establishment, although its price made it as easy for non luxurious individual, but for 
                    the yourng ment it was`,
                    A:'Human society was static without the express speed of cars and motorcycles',
                    B:'Significant improvement occured in road transportation since the advent of car lorries and motor cycles',
                    C:'Human society was static without the express speed of cars and motorcycles',
                    D:'Society would be better off without the chaotic volume and speed of motor cars lories and motor cars'
            
                },
                {
                    question:`1910, the car way already conquering the highway, the private car was now part of the 
                    rich man's establishment, although its price made it as easy for non luxurious individual, but for 
                    the yourng ment it was`,
                    A:'Human society was static without the express speed of cars and motorcycle',
                    B:'Significant improvement occured in road transportation since the advent of car lorries and motor cycles',
                    C:'Human society was static without the express speed of cars and motorcycles',
                    D:'Society would be better off without the chaotic volume and speed of motor cars lories and motor cars'
            
                },
                       
               ]
        ,
            current:1,
            index:0,
            finish:false
        }
    }
  
    render() {
        return (
            <React.Fragment>
                {
                 !this.state.finish?
<ExamContextProvider.Consumer>
                    {
                        ({selectedCourse})=>{
                           return(
                 <div className='container'>
                    <div className="test row">
                        <div className=" bg-primary text-top col-md-12 col-sm-12">
                                <h3>
                                    {selectedCourse}
                                </h3>
                                <span className="li_">
                                <ul >
                                
                                <li>
                                  08:05M
                                </li>
                                <li>
                                  00:08:05M 
                                </li>
 
                             </ul>
                                </span>
                            
                        </div>
                    </div>
                    <div className="row question_container">
                    <div className=" q_ col-md-12 col-sm-12">
                        <h6 className="question-header font-weight-bold" style={{marginTop:'-23px'}}>QUESTION</h6>
                        <div className="q_inner col-md-12 col-sm-12">
                            <p style={{fontWeight:'bold',fontStyle:'italic',fontSize:'16px',paddingBottom:'15px', paddingTop:'15px', color:'rgba(0,0,0,0.5)'}}>Read the Question carefully before you choose you answer</p>
                             <p style={{fontWeight:'bold',fontSize:'16px', color:'rgba(0,0,0,0.5)'}}>
                                {this.state.questions[this.state.index].question}
                             </p>
                            
                            
                             <p className="option" style={{fontWeight:'bold',fontSize:'16px', color:'rgba(0,0,0,0.5)'}}>
                             <input  type="radio" value="A" name="option"/><span style={{position:'absolute'}}>(A)</span>
                             <span className="ques"   style={{position:'absolute', left:'114px'}}>{this.state.questions[this.state.index].A}</span>
                             
                             </p>

                             <p className="option" style={{fontWeight:'bold',fontSize:'16px', color:'rgba(0,0,0,0.5)'}}>

                             <input  type="radio" value="B" name="option"/><span style={{position:'absolute'}}>(B)</span>
                             <span className="ques"  style={{position:'absolute', left:'114px'}}>{this.state.questions[this.state.index].B}</span>
                             
                             </p>

                             <p className="option" style={{fontWeight:'bold',fontSize:'16px', color:'rgba(0,0,0,0.5)'}}>

                             <input  type="radio" value="C" name="option"/><span style={{position:'absolute'}}>(C)</span>
                             <span className="ques"  style={{position:'absolute', left:'114px'}}>{this.state.questions[this.state.index].C} </span>
                
                             </p>

                             <p  className="option" style={{fontWeight:'bold',fontSize:'16px', color:'rgba(0,0,0,0.5)'}}>

                             <input  type="radio" value="D" name="option"/><span style={{position:'absolute'}}>(D)</span>
                             <span className="ques" style={{position:'absolute', left:'114px'}}>{this.state.questions[this.state.index].D}</span>

                             </p>

                            
                            

                             


                        </div>
                 <div className="q_selector col-md-12 col-sm-12">
                  
                     <div onClick={()=>this.setState((prevState)=>{
                         return {index:prevState.index ==0 ? 0: prevState.index-1}
                     })} style={{marginTop:'13px',padding:'3px 26px',fontSize:'13px' ,color:'rgb(39, 113, 223)'}} className=" btn btn-primary bg-transparent">
                           <span style={{position:"relative", top:'-2px'}}>PREVIOUS</span>
                      
                       <FontAwesomeIcon style={{
                           color:"rgb(39, 113, 223)", width:"19px", height:"19px",
                           display:'inline',
                           marginLeft:'13px',
                           
                           marginTop:'5px',
                          
                           }} icon={faChevronCircleLeft} />   
                   </div>


                   <div onClick={()=>this.setState((prevState)=>{
                         return {index:prevState.index == this.state.questions.length-1 ? this.state.questions.length-1: prevState.index+1}
                     })} style={{marginTop:'12px',padding:'3px 26px',fontSize:'13px' ,color:'rgb(39, 113, 223)'}} className=" btn btn-primary bg-transparent">
                           <span style={{position:"relative", top:'-4px'}}>NEXT</span>
                      
                       <FontAwesomeIcon style={{
                           color:"rgb(39, 113, 223)", width:"19px", height:"19px",
                           display:'inline',
                           marginLeft:'13px',
                           
                           marginTop:'5px',
                          
                           }} icon={faChevronCircleRight} />   
                   </div>


                   <div onClick={()=>{this.setState({finish:true})}} style={{marginTop:'12px',padding:'3px 26px',fontSize:'13px' ,color:'rgb(39, 113, 223)'}} className=" btn btn-primary bg-transparent">
                           <span style={{position:"relative", top:'-2px'}}>FINISH</span>
                      
                       <FontAwesomeIcon style={{
                           color:"rgb(39, 113, 223)", width:"19px", height:"19px",
                           display:'inline',
                           marginLeft:'13px',
                           
                           marginTop:'5px',
                          
                           }} icon={faStop} />   
                   </div>
                   </div>
                    </div>
                    </div>

                    <div className="row">
                        <div className="number__ col-md-12 col-sm-12">
                            {
                                this.state.questions.map((question, index)=>{
                                  return(
                                    <div key={index} onClick={()=>{
                                       
                                        this.setState({current:index+1})
                                    }} className= {(this.state.current==index+1?`light  btn btn-primary p-1`:`light btn btn-light p-1` )}>
                                        {index+1}
                                    </div>
                                  )
                                })
                            }
                            
                            
                           
                        </div>
                    </div>

                </div>
                           )
                        }
                    }
                </ExamContextProvider.Consumer>
                :
                <Answers answer={this.state.questions}/>
                }
                
            </React.Fragment>
        )
    }
}
